import { auth } from "@/lib/auth";
import { NextResponse } from "next/server";

export default auth((req) => {
  const isLoggedIn = !!req.auth;
  const isLoginPage = req.nextUrl.pathname === "/login";
  const isApiAuth = req.nextUrl.pathname.startsWith("/api/auth");
  const isEspApi = req.nextUrl.pathname.startsWith("/api/esp");
  const isPublicAsset = req.nextUrl.pathname.startsWith("/_next") ||
                        req.nextUrl.pathname.startsWith("/favicon");

  // Разрешаем публичные роуты
  if (isApiAuth || isEspApi || isPublicAsset) {
    return NextResponse.next();
  }

  // Редирект на дашборд если уже авторизован и пытается зайти на логин
  if (isLoginPage && isLoggedIn) {
    return NextResponse.redirect(new URL("/dashboard", req.url));
  }

  // Редирект на логин если не авторизован
  if (!isLoginPage && !isLoggedIn) {
    return NextResponse.redirect(new URL("/login", req.url));
  }

  return NextResponse.next();
});

export const config = {
  matcher: ["/((?!_next/static|_next/image|favicon.ico).*)"],
};
